const delateAll = 'delateAll11'
const ADDEVENT = 'ADDEVENT'
const EVENTDONE = 'EVENTDONE'
const RECOVER = 'RECOVER'
import * as func from './function.js'

export default {
  [delateAll] (state) {
    state.Larray1 = []
    func.local.clear()
  },
  [ADDEVENT] (state, obj) {
    state.counttag++
    obj.items.id = state.counttag
    state.Larray1.unshift(obj.items)
    func.local.set(state)
  },
  [EVENTDONE] (state, obj) {
    for (let i = 0; i < state.Larray1.length; i++) {
      if (state.Larray1[i].id === obj.id) {
        state.Larray1[i].type = 2
        break
      }
    }
    func.local.set(state)
  },
  [RECOVER] (state, obj) {
    for (let i = 0; i < state.Larray1.length; i++) {
      if (state.Larray1[i].id === obj.id) {
        state.Larray1[i].type = 1
        break
      }
    }
    func.local.set(state)
  }
}



