<template>
  <div>
      <el-button  type="primary" :plain="true" @click="handle">{{ Bname }}</el-button>
  </div>
</template>

<script>
export default {
  props: ['Bname'],
  data () {
    return {
      msg: 'ok'
    }
  },
  methods: {
    handle () {
      alert(this.msg)
      this.$emit('clickButton')
    }
  }
}
</script>

<style scoped>
.button1 {
  background-image: url(../assets/timg.jpg);
}
</style>



