import Vue from 'vue'
import VueRouter from 'vue-router'
import test from '../components/test'
import Table1 from '../components/111'

// 告诉Vue  我们要用 vue-router
Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'test',
      component: test
    },
    {
      path: '/l2',
      name: 'table1',
      component: Table1
    }
  ]
})
// VueRouter   =>    库

