export default {
  getToDo (state) {
    return state.Larray1.filter(function (d) {
      if (d.type === 1) {
        return d
      }
    })
  },
  getDone (state) {
    return state.Larray1.filter(function (d) {
      if (d.type === 2) {
        return d
      }
    })
  }
}



