<<template>
 <div>
  <span>请留言: <el-input v-model="msg2" placeholder="输入" @keyup.enter="addMsg"></el-input></span>
  <br>
  <el-button type="primary" @click="addMsg">留言</el-button>
  <ul class="ul-no">
  <li v-for="value in getToDo">
      <input type="checkbox" class="pos-imag1" :key="value.id" @click="moveToDone(value.id)">
      <span class="pos-imag2">{{value.msg2}}</span>
      <c-button :index="value.id" class="pos"></c-button>
      <br>
      <br>
  </li>
  </ul>
  <div>
    <p>已读</p>
    <ul class="ul-no">
      <li v-for="value in getDone">
      <span>{{value.msg2}}</span>
      <button @click="recover(value.id)">恢复数据</button>
      </li>
    </ul>
  </div>
  <el-button type="danger" icon="delete" @click="delateAll">删除全部</el-button>
  <p>{{Larray1}}</p>
  </div>
</template>

<script>
import canclebutton from '@/components/canclebutton'
export default {
  data () {
    return {
      msg2: ''
    }
  },
  components: {
    'c-button': canclebutton
  },
  methods: {
    addMsg () {
      let self = this
      let params = {
        type: 1,
        id: 0,
        msg2: ''
      }
      self.msg2 = self.msg2.trim()
      if (self.msg2) {
        params.msg2 = self.msg2
        self.$store.dispatch('addevent', params)
        self.msg2 = ''
      }
    },
    moveToDone (id) {
      this.$store.dispatch('eventdone', id)
    },
    delateAll () {
      this.$store.commit('delateAll11')
    },
    recover (id) {
      this.$store.dispatch('recover', id)
    }
  },
  computed: {
    getToDo () {
      return this.$store.getters.getToDo
    },
    getDone () {
      return this.$store.getters.getDone
    }
  }
}
</script>

<style>
.ul-no{
list-style-type: none;
}
.pos{
    position:absolute;
    left: 1220px;
    display: inline
}
.pos-imag1{
  position: relative;
  top:10px;
  left:-250px
}
.pos-imag2{
  position: relative;
  top:10px;
  left:-30px
}
</style>



