<<template>
<div>
  <el-button type="danger" icon="delete" @click="canclemsg">删除{{index}}</el-button>
</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  props: ['index'],
  data () {
    return {
      tag: this.index
    }
  },
  methods: {
    canclemsg () {
      this.Larray1.splice(this.Larray1.length - this.index, 1, [])
    }
  },
  computed: mapState([
    'Larray1'
  ])
}
</script>

<style>

</style>



