<<template>
  <div v-if="conter%2 == 0">
  <p>{{ Mpage }}</p>
  <p>名字:{{Name}}</p>
  <z-button @clickButton="handle" Bname="切换语言"></z-button>
  <p>年龄:{{Age}}</p>
  <p>邮件:{{Email}}</p>
  <p>电话:{{Phone}}</p>
  <router-link to="/l2" class="bgimg button_style">给我留言</router-link>
  </div>
  <div v-else>
  <p>{{ Mpage }}</p>
  <p>Name:{{EnName}}</p>
  <z-button @clickButton="handle" Bname="Exchange Language"></z-button>
  <p>Age:{{Age}}</p>
  <p>E-mail:{{Email}}</p>
  <p>Phone:{{Phone}}</p>
  <router-link to="/l2" class="bgimg">Go Leave Message</router-link>
  </div>

</template>

<script>
import B from '../components/Button'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      Name: '张雨驰',
      conter: 2,
      Mpage: '主页'
    }
  },
  components: {
    'z-button': B
  },
  methods: {
    handle () {
      this.conter = this.conter + 1
    }
  },
  computed: mapState([
    'Age',
    'Email',
    'Phone',
    'EnName'
  ])
}
</script>

<style>
</style>



